import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import axios from "axios";

function Resume() {
  const uri = "https://porfolio-backend.vercel.app/ranks/getRanks";
  const [upadteSpojRank] = useState(0);
  const [upadteHackerank] = useState(0);
  const [upadateSem] = useState(0);
  const [upadteCgpa] = useState(0);

  useEffect(() => {
    axios
      .get(uri)
      .then((res) => {
        upadteSpojRank(res.data.message[0].spojRank);
        upadteHackerank(res.data.message[1].hackerrank);
        upadteCgpa(res.data.message[2].cgpa);
        upadateSem(res.data.message[3].sem);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Container fluid className="resume-section">
      <Particle />
      <Container>
        <Row className="resume">
          <Col md={6} className="resume-left">
            <h3 className="resume-title">Experience</h3>
            <Resumecontent
              title="–≤–µ–±-—Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ 'Laika'"
              date="–ú–∞–π 2019 - –û–∫—Ç—è–±—Ä—å 2020"
              content={[
                "–≤–µ—Ä—Å—Ç–∫–∞ –º–∞–∫–µ—Ç–æ–≤",
                "–ø–æ—Å–∞–¥–∫–∞ –≤–µ—Ä—Å—Ç–∫–∏ –Ω–∞ Wordpress",
                "—Å–æ–∑–¥–∞–Ω–∏–µ —à–∞–±–ª–æ–Ω–æ–≤ –Ω–∞ Wordpress",
              ]}
            />
            <Resumecontent
              title="frontend developer 'ADCI Solutions'"
              date="–û–∫—Ç—Ä—è–±—Ä—å 2020 - –ø–æ –Ω–∞—Å—Ç–æ—è—â–µ–µ –≤—Ä–µ–º—è"
              content={["—Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∞ –≤–µ–± –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–π –Ω–∞ React JS"]}
            />
          </Col>
          <Col md={6} className="resume-right">
            <h3 className="resume-title">Education</h3>
            <Resumecontent
              title="–û–º–ì–ê–£ –∏–º –ü.–ê. –°—Ç–æ–ª—ã–ø–∏–Ω–∞"
              date="2011 - 2015"
              content={["–ò–Ω–∂–µ–Ω–µ—Ä-—Ç–µ—Ö–Ω–æ–ª–æ–≥ –ø—Ä–æ–¥—É–∫—Ç–æ–≤ –∏–∑ —Ä–∞—Å—Ç–∏—Ç–µ–ª—å–Ω–æ–≥–æ —Å—ã—Ä—å—è"]}
            />
            {/* <h3 className="resume-title">Ranks and Achivements</h3> */}
            {/* <Resumecontent
              title=""
              content={[
                `Current rank in Spoj ${spojRank}`,
                `Current rank in HackerRank  ${hackerrank}`,
                "Top Performer in Code-Break 1.0",
                "Participant in Hack-A-Bit 2019",
              ]}
            /> */}
          </Col>
        </Row>
        {/* <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href={pdf} target="_blank">
          <AiOutlineDownload />&nbsp;Download CV
          </Button>
        </Row> */}
      </Container>
    </Container>
  );
}

export default Resume;
